.joinGameContainer {
  margin: 10vh 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.nameInput {
  margin-bottom: 20px;
}

.codeInput {
  margin-bottom: 20px;
}

.takePhotoButton {
  margin: 10px 0 20px;
  width: 100%;
}

.avatar {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  object-fit: cover;
  margin: auto;
}

.joinButton {
  width: 100%;
}